<template>
  <div class="main">
    <div v-html="activity.content_start.replace(/\n/g, '<br/>')" class="text">
    </div>
    <div class="content relative">
      <div class="border-top"></div>
			<div class="arrow-right"></div>
			<div class="count">{{ storyData.content.length }}/140</div>
			<textarea v-model="storyData.content" class="textarea" placeholder="请在此输入你的故事，140个字符以内" />
    </div>
    <div class="name">
			<div>
				<span class="name-text">你的昵称是？</span>
			</div>	
			<div class="relative">
				<div class="border-top"></div>
				<div class="arrow-right"></div>
				<input v-model="storyData.nickName" class="input" type="text" placeholder="请在此处输入昵称，5个字符以内" />
			</div>  
    </div>
		<div class="btn center">
			<span @touchend="addStory"></span>
		</div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import ajax from '@/libs/request'
export default {
  name: 'Add',
  components: {

  },
  data () {
    return {
      storyData: {
				nickName: '',
				content: '',
				userId: '',
				activityId: ''
			}
    }
	},
	computed: {
    ...mapState({
      activity: state => state.app.activity,
      userId: state => state.app.userInfo.id
		})
	},
	watch: {
		'storyData.content': function(v) {
			if (v.length > 140) {
				this.storyData.content = this.storyData.content.substr(0, 140)
			}
		},
		'storyData.nickName': function(v) {
			if (v.length > 5) {
				this.storyData.nickName = this.storyData.nickName.substr(0, 5)
			}
		}
	},
	methods: {
		ajax(req, params = {}) {
      this.$emit('show-loading', '正在提交，请稍后...')
      return ajax[req](params).then(response => {
        this.$emit('hide-loading')
        const data = response.data
        if (data.code === 0) {
          return data.data || true
        } else {
          this.$emit('show-loading', data.msg, 2000)
          return false
        }
      }).catch(() => {
				this.$emit('show-loading', '请求失败，请重试', 2000)
				return false
      })
		},
		async addStory () {
			this.storyData.userId = this.userId
			this.storyData.activityId = this.activity.id
			if (!this.storyData.nickName || !this.storyData.content) {
				this.$emit('show-loading', '请输入完整内容', 2000)
				return
			}
			const result = await this.ajax('storyAdd', this.storyData)
			if (result) {
				this.$store.dispatch('setHasStory', this.activity.code)
			}
		}
	}
}
</script>

<style scoped>
	.main{
		padding: 0 3rem 3rem 3rem
	}
	.text{
		font-weight: bold;
		line-height: 2rem;
		margin: 3rem 0 1rem;
		/* white-space: pre; */
	}
	.content .textarea{
		border: 1px solid #333;
		resize: none;
		width: 100%;
		height: 10rem;
		padding:5px;
		box-sizing:border-box;
	}
	.name .input{
		border:1px solid #333;
		width: 100%;
		padding:5px;
		box-sizing:border-box;
	}
	.name .name-text{
		display: inline-block;
		padding: 2px;
		background:#38415e;
		font-weight: bold;
		color: #fff;
		margin: 2rem 0 1rem;
	}
	.btn span{
		display: inline-block;
		margin-top: 2rem;
		width: 75px;
		height: 33px;
		background: url('../../static/image/submit-btn.png') no-repeat;
		background-size: 100% 100%
	}
	.count{
		position: absolute;
		right: 0;
		top: -15px;
		color: #666;
		font-size: 1.2rem
	}
</style>



