function getStrLen (str) { // 获取字符串长度
  return str.replace(/[\u0391-\uFFE5]/g, 'aa').length
}

function cutStrByLen (str, len) { // 截取字符串
  let charLength = 0
  for (let i = 0; i < str.length; i++) {
    let sonStr = str.charAt(i)
    encodeURI(sonStr).length > 2 ? charLength += 1 : charLength += 0.5
    if (charLength >= len) {
      let subLen = charLength === len ? i + 1 : i
      return str.substr(0, subLen)
    }
  }
}

function getUrlParams () {
  let url = location.search
  let theRequest = {}
  if (url.indexOf('?') !== -1) {
    let str = url.substr(1)
    let strs = str.split('&')
    for (let item of strs) {
      theRequest[item.split('=')[0]] = unescape(item.split('=')[1])
    }
  }
  return theRequest
}
export {
  getStrLen,
  cutStrByLen,
  getUrlParams
}



