<template>
  <div class="main">
    <div v-html="activity.content_share.replace(/\n/g, '<br/>')" class="text">
    </div>
		<div class="tip center">
			<div class="img"></div>
		</div>
		<div class="qrcode center">
			<div class="img"></div>
		</div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'Join',
  components: {
    
  },
  data () {
    return {
      
    }
	},
	computed: {
    ...mapState({
      activity: state => state.app.activity
		})
	},
}
</script>

<style scoped>
	.main{
		padding: 0 3rem 3rem 5rem
	}
	.text{
		line-height: 2rem;
		margin: 3rem 0 1rem
	}
	.tip{
		margin: 3rem 0 3rem -2rem
	}
	.tip .img{
		display: inline-block;
		width: 245px;
		height: 81px;
		background: url('../../static/image/qrcode-tips.png') no-repeat;
		background-size: 100% 100%
	}
	.qrcode{
		margin: 3rem 0 3rem -2rem
	}
	.qrcode .img{
		display: inline-block;
		width: 125px;
		height: 125px;
		background: url('../../static/image/qrcode.png') no-repeat;
		background-size: 100% 100%
	}
</style>



