<template>
  <div id="app">
    <div v-if="activity && userInfo" class="banner">
      <img width="100%" :src="activity.banner" />
    </div>
    <!-- <p v-show="!activity || !userInfo" class="center" style="margin-top: 50px">数据加载中...</p> -->
    <div v-if="activity && userInfo" style="height: 100%;">
      <component
        @show-loading="showLoading"
        @hide-loading="hideLoading"
        @show-share-guid="shareGuid=true"
        :is="currentView"
      />
    </div>
    <div v-show="loadingStatus || (!activity || !userInfo)" class="loading">
      <Loading :text="loadingText" />
    </div>
    <div v-show="shareGuid" @touchend="shareGuid=false" class="share-guid">
      <div class="img"></div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Add from './components/Add'
import Show from './components/Show'
import Join from './components/Join'
import Share from './components/Share'
import Loading from './components/Loading'
import { getUrlParams } from '@/libs/utils'

export default {
  name: 'App',
  components: {
    Add,
    Show,
    Join,
    Share,
    Loading
  },
  data () {
    return {
      loadingText: '数据加载中...',
      loadingStatus: false,
      shareGuid: false
    }
  },
  computed: {
    ...mapState({
      activity: state => state.app.activity,
      userInfo: state => state.app.userInfo,
      hasStory: state => state.app.hasStory
    }),
    currentView () {
      const params = getUrlParams()
      if (!this.activity.finish) {
        if (this.hasStory) {
          if (params.action != 'share') {
            location.href = location.href + '&action=share'
          }
          return 'Share'
        } else {
          if (params.action == 'share') {
            return 'Join'
          } else {
            return 'Add'
          }
        }
      } else if (this.userInfo) {
        return 'Show'
      } else {
        return 'Add'
      }
    }
  },
  mounted () {
    const wx = navigator.userAgent.indexOf('micromessenger')
    if (wx == -1) {
      // this.showLoading('请在微信中打开')
      // return
    }
    this.$store.dispatch('setUserInfo')
    const params = getUrlParams()
    if (params.code) {
      this.$store.dispatch('setActivity', params.code)
      this.$store.dispatch('setHasStory', params.code)
    }
  },
  methods: {
    showLoading (text = '数据加载中...', time = 0) {
      this.loadingText = text
      this.loadingStatus = true
      if (time > 0) {
        window.setTimeout(() => {
          this.loadingStatus = false
        }, time)
      }
    },
    hideLoading () {
      this.loadingStatus = false
    },
  }
}
</script>

<style>
body{
  margin: 0;
  padding: 0;
  font-size: 1.4rem;
}
#app {
  font-family: SimHei,'黑体',Microsoft YaHei,微软雅黑,MicrosoftJhengHei,华文细黑,STHeiti,MingLiu;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
}
.center{
  text-align: center
}
.border-top{
  height: 7px;
  background: #f1cbce;
  border: 1px solid #333;
  border-bottom: none
}
.arrow-right{
  position: absolute;
  right: 5px;
  top: 3px;
  width: 15px;
  height: 15px;
  background: url('../static/image/arrow-right.png') no-repeat;
  background-size: 100% 100%
}
.relative{
  position: relative;
}
</style>
<style scoped>
  .loading{
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100
  }
  .share-guid{
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 50;
    background: #000;
    opacity: 0.7
  }
  .share-guid .img{
    width: 100%;
    height: 100%;
    background: url('../static/image/share-guid.png') no-repeat;
    background-size: 100% 100%
  }
</style>




