import ajax from '@/libs/request'
const app = {
  state: {
    activity: null,
		userInfo: null,
		hasStory: false // 是否已经存在此活动的故事
  },
  mutations: {
    SET_ACTIVITY: (state, activity) => {
      state.activity = activity
    },
    SET_USER_INFO: (state, userInfo) => {
      state.userInfo = userInfo
		},
		SET_HAS_STORY: (state, hasStory) => {
      state.hasStory = hasStory
    }
  },
  actions: {
    setActivity({ commit }, code) {
			const query = { code }
			return ajax.activityGet(query).then(response => {
				if (response.data.code === 0) {
					commit('SET_ACTIVITY', response.data.data)
					return true
				} else {
					return false
				}
			}).catch(() => {
				return false
			})
    },
    setUserInfo({ commit }) {
			const query = {
				userId: window.localStorage.userId
			}
			return ajax.getUser(query).then(response => {
				if (response.data.code === 0) {
					const userInfo = response.data.data
					window.localStorage.userId = userInfo.id
					commit('SET_USER_INFO', userInfo)
					return true
				} else {
					return false
				}
			}).catch(() => {
				return false
			})
		},
		setHasStory({ commit }, code) {
			const userId = window.localStorage.userId
			if (userId) {
				const query = {
					userId,
					code
				}
				return ajax.storyGet(query).then(response => {
					if (response.data.code === 0) {
						commit('SET_HAS_STORY', true)
						return true
					} else {
						commit('SET_HAS_STORY', false)
					}
					return true
				}).catch(() => {
					return false
				})
			}
			
    }
  }
}

export default app



