import axios from 'axios'
const request = axios.create({
  baseURL: 'http://139.129.240.72/dingyuehao-server/public/index.php/web',
  timeout: 5000, // request timeout
})
class Ajax {

	getUser(query) {
		return request({
			url: 'user/add',
			method: 'post',
			data: query
		})
	}
	storyAdd(query) {
		return request({
			url: 'story/add',
			method: 'post',
			data: query
		})
	}
	storyGet(query) {
		return request({
			url: 'story/get',
			method: 'post',
			data: query
		})
	}
	storyMatch(query) {
		return request({
			url: 'story/match',
			method: 'post',
			data: query
		})
	}
	activityGet(query) {
		return request({
			url: 'activity/get',
			method: 'post',
			data: query
		})
	}
}
export default new Ajax



