<template>
  <div class="main">
    <div v-html="activity.content_end.replace(/\n/g, '<br/>')" class="text">
    </div>
		<div class="name">
			<div>
				<span class="name-text">TA的昵称</span>
			</div>
			<div class="relative">
				<div class="border-top"></div>
				<div class="arrow-right"></div>
				<div class="input">
					{{ storyDetail.otherStory.nick_name }}
				</div>
			</div>  
    </div>
		<div>
			<div>
				<span class="name-text">TA的故事</span>
			</div>
			<div class="content relative">
				<div class="border-top"></div>
				<div class="arrow-right"></div>
				<div v-html="storyDetail.otherStory.content.replace(/\n/g, '<br/>')" class="textarea" >
				</div>
			</div>
		</div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import ajax from '@/libs/request'
export default {
  name: 'Show',
  components: {
    
	},
  data () {
    return {
      storyDetail: {
				otherStory: {
					content: ''
				}
			}
    }
	},
	computed: {
    ...mapState({
      activity: state => state.app.activity,
      userId: state => state.app.userInfo.id
		})
	},
	async mounted () {
		const query = {
			userId: this.userId,
			activityId: this.activity.id
		}
		const result = await this.ajax('storyMatch', query)
		if (result) {
			this.storyDetail = result
		}
	},
	methods: {
		ajax(req, params = {}) {
      return ajax[req](params).then(response => {
        const data = response.data
        if (data.code === 0) {
          return data.data || true
        } else {
          this.$emit('show-loading', data.msg, 2000)
          return false
        }
      }).catch(() => {
				this.$emit('show-loading', '数据获取失败', 2000)
				return false
      })
		},
	}
}
</script>

<style scoped>
	.main{
		padding: 0 3rem 3rem 3rem
	}
	.text{
		line-height: 2rem;
		margin: 3rem 0 1rem
	}
	.content .textarea{
		border: 1px solid #333;
		resize: none;
		width: 100%;
		height: 10rem;
		padding:5px;
		box-sizing:border-box;
	}
	.name .input{
		border:1px solid #333;
		width: 100%;
		padding:5px;
		box-sizing:border-box;
	}
	.name-text{
		display: inline-block;
		padding: 2px;
		background:#38415e;
		font-weight: bold;
		color: #fff;
		margin: 1.5rem 0 1rem;
	}
</style>



