<template>
  <div class="main center">
    <div class="success">
			<span class="img"></span>
			<p>点击下方分享按钮</p>
			<p>完成报名</p>
		</div>
		<div class="msg relative">
			<span class="img"></span>
			<div class="msg-text">
				<p>分享完毕后</p>
				<p>{{ endDateText }}将在订阅号“买买菌”</p>
				<p>为你公布交换结果</p>
			</div>
			<p class="no-share">(如果不分享，本次报名将无效噢)</p>
		</div>
		<div @touchend="share" class="btn">
			<span class="img"></span>
		</div>
		<div class="tips">
			<span class="img"></span>
		</div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'Join',
  components: {
    
  },
  data () {
    return {
      
    }
	},
	computed: {
    ...mapState({
      endDateText: state => state.app.activity.end_date_text
		})
	},
	methods: {
		share () {
			this.$emit('show-share-guid')
		}
	}
}
</script>

<style scoped>
	.main .img{
		display: inline-block
	}
	.success .img{
		width: 120px;
		height: 120px;
		background: url('../../static/image/success.png') no-repeat;
		background-size: 100% 100%;
		margin: 4rem 0 2rem
	}
	.msg .img{
		width: 280px;
		height: 85px;
		background: url('../../static/image/msg.png') no-repeat;
		background-size: 100% 100%;
	}
	.msg .msg-text{
		position: absolute;
		top: 0.6rem;
		left: 0;
		width: 100%;
	}
	.msg .msg-text p{
		font-weight: bold;
		line-height: 0.6rem;
		font-family: Microsoft YaHei, 微软雅黑
	}
	.msg p.no-share{
		font-size: 1.2rem;
		margin: 0.5rem 0 2rem
	}
	.btn .img{
		width: 75px;
		height: 30px;
		background: url('../../static/image/share-btn.png') no-repeat;
		background-size: 100% 100%;
	}
	.tips{
		margin-top: 0.5rem
	}
	.tips .img{
		width: 100px;
		height: 60px;
		background: url('../../static/image/tips.png') no-repeat;
		background-size: 100% 100%;
	}
</style>



