<template>
	<div class='mock'>
		<div class='main'>
				<span>{{ text }}</span>
		</div>
	</div>
</template>
<script>
export default {
	name: 'Loading',
	props: {
		text: {
      type: String,
      default: '数据加载中...'
    }
	},
	data () {
    return {
      
    }
	}
}
</script>
<style scoped>
	.mock{
		width:100%;
		height:100%;
	}
	.main{
		padding-top:200px;
		text-align:center;
	}
	.main span{
		display: inline-block;
		background: #000;
		color: #fff;
		border-radius: 5px;
		padding: 10px 20px
	}
</style>


